.sibe-bar-container {
    background-color: burlywood;
    width: 250px;
    min-width: 250px;
    height: calc(100vh - 70px);

    &__list {
        list-style: none;
        padding: 0;
    }

    &__list-item {
        padding: 5px 10px;
        text-align: center;
        font-size: 20px;
        font-weight: 600;
        cursor: pointer;
        border-bottom: 2px solid #000;
    }
    .active {
        background-color: sandybrown;
    }
}