.task-form-wrapper {
    display: flex;
    justify-content: center;
    margin-left: 100%;
    width:100%;

    .task-form-container {
        width: 400px;
        display: flex;
        flex-direction: column;
        row-gap: 10px;
    }

    .task-form {
        display: flex;

        &__label {
            min-width: 110px;
        }
    }
    .task-form-btn {
        display: flex;
        justify-content: center;
        column-gap: 10px;
        margin-left: 25%;
    }
}

